/* Media query para pantallas entre 768px y 991px */

@media (min-width: 768px) and (max-width: 991px) {

  /* HEADER */
  header {
    padding: 1rem;
  }

  .company-logo {
    font-size: 2rem;
  }

  .nav-items {
    display: none;
  }

  .menu-toggle {
    display: block;
  }

  /* MAIN */
  .section-1 {
    grid-template-columns: 1fr;
    justify-content: center;
    padding: 5rem 1rem;
  }

  .section-1 .company-title {
    font-size: 1.2rem;
    max-width: 20rem;
  }

  .section-1 .company-slogan {
    font-size: 1rem;
    max-width: 15rem;
  }

  .section-2 {
    margin: 2rem 0;
  }

  .offer {
    grid-template-rows: auto;
    grid-template-areas: "offer-1-img" "offer-desc-1";
    margin: 2rem 0;
  }

  .offer img {
    width: 100%;
  }

  .offer2 {
    grid-template-areas: "offer-desc-2" "offer-2-img";
    margin-left: 0;
  }

  .offer-description {
    font-size: 1.2rem;
  }

  .offer-description .offer-title {
    font-size: 1.2rem;
    padding: 1rem 0 0.5rem 0;
  }

  .offer-description .offer-hook {
    font-size: 1rem;
  }

  .offer-description .cart-btn {
    font-size: 1rem;
    width: 8rem;
    height: 2.5rem;
  }

  /* FOOTER */
  .footer-title {
    font-size: 1.1rem;
  }

  .footer-items h3 {
    font-size: 1rem;
  }

  .end-footer {
    min-height: 20vh;
  }

  .icon-3d {
    font-size: 2rem;
  }

}



